@import '../../utils/mixins';
@import '../../utils/vars';

.footer {
  display: flex;
  // position: relative;
  flex-direction: column;
  justify-content: flex-end;
  height: 100vh;
  width: 100%;
  

  &__content {
    color: #fff;
    background-color: var(--total-black);
    position: relative;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px 0;

    @include onDesktop {
      height: 100px;
    }

    &--social-icon {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 10px 0;
      flex-wrap: wrap;
      z-index: 1005;
    }

    &--list {
      list-style: none;
    }

    &--icon-container {
      display: inline-block;
      margin: 0 10px;
      transition: 0.5s;

      &:hover {
        transform: translateY(-10px);
      }
    }
  }
}

.menu {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  &__list {
    margin: 0 10px;
    list-style: none;
  }

  &__item {
    text-decoration: none;
    color: #fff;
    opacity: 0.75;
    font-size: 16px;

    &:hover {
      opacity: 1;
    }
  }
}

.wave {
  position: absolute;
  top: -99px;
  left: 0;
  width: 100%;
  height: 100px;
  background: url(../../assets/images/wave.png);
  background-size: 1000px 100px;
}

#wave1 {
  z-index: 1000;
  opacity: 1;
  bottom: 0px;
  animation: animateWave 4s linear infinite;
}

#wave2 {
  z-index: 999;
  opacity: 0.5;
  bottom: 10px;
  animation: animateWave_2 4s linear infinite;
}

#wave3 {
  z-index: 999;
  opacity: 0.2;
  bottom: 15px;
  animation: animateWave 3s linear infinite;
}
#wave4 {
  z-index: 999;
  opacity: 0.7;
  bottom: 20px;
  animation: animateWave_2 3s linear infinite;
}

@keyframes animateWave {
  0% {
    background-position-x: 1000px;
  }
  100% {
    background-position-x: 0px;
  }
}

@keyframes animateWave_2 {
  0% {
    background-position-x: 0px;
  }
  100% {
    background-position-x: 1000px;
  }
}